.overlay {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
}
.iframe {
  min-width: 500px;
  width: 50vw;
  position: fixed;
  top: 50%;
  left: 50%;
  height: 70vh;
  z-index: 12;
  -webkit-transform: translateX(-50%) translateY(-50%);
  transform: translateX(-50%) translateY(-50%);
}
.close {
  position: absolute;
  z-index: 12;
  right: 2rem;
  top: 2rem;
  width: 4rem;
  height: 4rem;
  background: url(data:image/png;base64,) 0 0 no-repeat;
  background-size: cover;
}
